body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html{
  background: #1D976C;  /* fallback for old browsers */
background: -webkit-linear-gradient(to left, #93F9B9, #1D976C);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to left, #93F9B9, #1D976C); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

}

.nav{
  background: #1D976C;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to left, #93F9B9, #1D976C);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to left, #93F9B9, #1D976C); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.content{
  background: #1D976C;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to left, #93F9B9, #1D976C);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to left, #93F9B9, #1D976C); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.navBrand{
  margin-left: 30px;
  cursor: default;
  font-size: 25px;
}

.navLink{
  margin: 2px 50px 0 15px;
}

.link-container{
  margin-left: auto!important;
}

.bitmoji{
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 250px;
}

.Typist{
  text-align: center;
  font-size: 50px;
}

a{
  text-decoration: none !important;
  color: black !important;
}

.banner{
  background-color: rgba(0, 0, 0, .4);
  width: 75%;
  margin: auto;
  opacity: 0.8;
  border-radius: 5pc;
  text-align: center;
}

.banner p{
  font-weight: bold;
  color:white;
  font-size: 50px;
}

h2{
  color:white;
}

.socials{
  display: block;
  margin: auto;
  width: 200px;
}